.table {
  width: 100%;
}
.text {
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.tableContainer th {
  border-left: none;
  position: sticky;
  top: 0;
  color: #2a5067;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  background-color: #e2f8fb;
  gap: 10px;
  height: 48px;
}

.tableContainer td {
  border-left: none;
  border-bottom: 1px solid #e8e9e9;
  padding: 5px;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.tableContainer {
  // width: 678px;
  overflow: auto;
  min-height: 95px;
  max-height: 300px;
  border: 1px solid;
  border-color: #d2d3d4;
  border-radius: 16px;
}
.tableContainer::-webkit-scrollbar {
  width: 0.3em;
  margin-left: 10px;
}
.error{
  position: absolute;
  top:60%
}
.tableContainer::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgb(255, 255, 255);
  outline: 0.2px solid rgba(183, 186, 189, 0.331);
  border-radius: 5%;
}

.tableContainer::-webkit-scrollbar-thumb {
  background-color: rgb(209, 204, 204);
  outline: 0.2px solid rgba(183, 186, 189, 0.331);
  border-radius: 5%;
}

.tableContainer table {
  border-spacing: 0;
}

.tableContainer th:nth-child(1) {
  padding: 0px 0px 0px 15px;
  z-index: 1;
  border-right: 1px solid #d2d3d4;
}
.tableContainer th:nth-child(2) {
  z-index: 1;
  padding: 0px 0px 0px 15px;
  border-right: 1px solid #d2d3d4;
}
.tableContainer th:nth-child(3) {
  z-index: 1;
  padding: 0px 0px 0px 15px;
  border-right: 1px solid #d2d3d4;
}
.tableContainer th:nth-child(4) {
  z-index: 1;
  padding: 0px 0px 0px 15px;
  border-right: 1px solid #d2d3d4;
}

.container {
  width: 1252px;
  margin-top: 24px;
  background-color: #e2f8fb;
  border-radius: 16px;
}

.tableContainer td {
  padding: 14px 15px;
  position: relative;
  // background: var(--accent-c-109, #F3F5E7);
}

.dropdownArrow {
  margin: 10px;

  cursor: pointer;
}
.dropdownTable {
  background: var(--primary-10, #fafbfb);
}
.color {
  background: var(--accent-c-109, #f3f5e7);
}
.inputfield {
  border: none;
  background: var(--accent-c-109, #f3f5e7);
}
.inputBorder {
  border: none;
  outline: none;
  background: var(--primary-10, #fafbfb);
}
